.blur-screen {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    color: white;
    font-size: 4em;
    z-index: 9999;
    cursor: pointer;
  }
  @keyframes float {
    0%, 100% {
      transform: translateY(0); /* –ò—Å—Ö–æ–¥–Ω–æ–µ –ø–æ–ª–æ–∂–µ–Ω–∏–µ */
    }
    50% {
      transform: translateY(-20px); /* –£–≤–µ–ª–∏—á–∏–≤–∞–µ–º –∞–º–ø–ª–∏—Ç—É–¥—É –¥–≤–∏–∂–µ–Ω–∏—è */
    }
  }
  
  .blur-screen {
    animation: float 2s ease-in-out infinite; /* –£–º–µ–Ω—å—à–∞–µ–º –¥–ª–∏—Ç–µ–ª—å–Ω–æ—Å—Ç—å –∞–Ω–∏–º–∞—Ü–∏–∏ */
  }