}
@keyframes magic-sparkle {
    0% { filter: drop-shadow(0 0 5px #fdf1f0); }
    100% { filter: drop-shadow(0 0 20px #a4e3ed); }
}

@keyframes pixel-blink {
    0% { opacity: 1; }
    50% { opacity: 0.7; }
    100% { opacity: 1; }
}


