body {
  margin: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  background-color: #f0f0f0;
}

.game-container {
  text-align: center;
  position: relative; /* –î–ª—è –ø–æ–∑–∏—Ü–∏–æ–Ω–∏—Ä–æ–≤–∞–Ω–∏—è –±–ª—é—Ä–∞ */
}

.background-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(255, 255, 255, 0); /* –ü—Ä–æ–∑—Ä–∞—á–Ω—ã–π —Ñ–æ–Ω */
  z-index: -1; /* –ü–æ–¥ –≤—Å–µ–º–∏ —ç–ª–µ–º–µ–Ω—Ç–∞–º–∏ */
  transition: filter 0.5s ease;
}

.background-overlay.blurred {
  filter: blur(5px); /* –ë–ª—é—Ä —Ñ–æ–Ω–∞ */
}

.game-title {
  position: relative;
  top: -30px;
  margin: 0;
  font-size: 26px;
  background: rgb(162, 18, 18); /* –ü–æ–ª—É–ø—Ä–æ–∑—Ä–∞—á–Ω—ã–π —Ñ–æ–Ω */
  box-shadow: 
    inset 0 6px 16px rgba(223, 223, 223, 0.488); /* –í–Ω—É—Ç—Ä–µ–Ω–Ω—è—è —Ç–µ–Ω—å –¥–ª—è –æ–±—ä—ë–º–∞ */
    border: 1px solid rgb(242, 242, 242); /* –õ—ë–≥–∫–∞—è –æ–±–≤–æ–¥–∫–∞ –¥–ª—è –æ–±—ä—ë–º–∞ */
  padding: 5px 15px; /* –ö–æ–º–ø–∞–∫—Ç–Ω—ã–µ –æ—Ç—Å—Ç—É–ø—ã */
  border-radius: 18px; /* –ó–∞–∫—Ä—É–≥–ª–µ–Ω–Ω—ã–µ –∫—Ä–∞—è */
  display: inline-block; /* –ß—Ç–æ–±—ã –∫–æ–Ω—Ç–µ–π–Ω–µ—Ä –ø–æ–¥—Å—Ç—Ä–æ–∏–ª—Å—è –ø–æ–¥ —Ä–∞–∑–º–µ—Ä —Ç–µ–∫—Å—Ç–∞ */
  z-index: 10;
}

.matryoshkas {
  display: flex;
  justify-content: center;
  gap: 25px;
  margin-top: 20px;
  z-index: 10; /* –ß—Ç–æ–±—ã –º–∞—Ç—Ä–µ—à–∫–∏ –±—ã–ª–∏ –Ω–∞–¥ –±–ª—é—Ä–æ–º */
}

.matryoshka {
  position: relative;
  cursor: pointer;
  width: 100px;
  height: 200px;
  top: -30px; /* –ü–æ–¥–Ω–∏–º–∞–µ–º –º–∞—Ç—Ä–µ—à–∫–∏ */
  filter: drop-shadow(0 4px 6px rgba(0, 0, 0, 0.2)); /* –¢–µ–Ω—å –ø–æ–¥ –º–∞—Ç—Ä–µ—à–∫–æ–π */
  z-index: 10; /* –ß—Ç–æ–±—ã –º–∞—Ç—Ä–µ—à–∫–∏ –±—ã–ª–∏ –Ω–∞–¥ –±–ª—é—Ä–æ–º */
  animation: appear 2s ease-out, pulse 1.5s infinite ease-in-out;
}

@keyframes appear {
  0% {
    opacity: 0;
    transform: scale(0.5);
  }
  100% {
    opacity: 1;
    transform: scale(1);
  }
}

@keyframes pulse {
  0%, 100% {
    transform: scale(1);
  }
  50% {
    transform: scale(1.05);
  }
}

.matryoshka-body img,
.matryoshka-top img {
  width: 125%;
  height: auto;
  position: relative;
  left: 50%;
  transform: translateX(-50%);
}

.matryoshka-top {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 10; /* –ß—Ç–æ–±—ã –≤–µ—Ä—Ö–Ω—è—è —á–∞—Å—Ç—å –º–∞—Ç—Ä–µ—à–∫–∏ –±—ã–ª–∞ –Ω–∞–¥ –±–ª—é—Ä–æ–º */
}


.promo-ticket-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: rgba(0, 0, 0, 0.5); /* –ü–æ–ª—É–ø—Ä–æ–∑—Ä–∞—á–Ω—ã–π —Ñ–æ–Ω */
  z-index: 20; /* –ö–∞—Ä—Ç–æ—á–∫–∞ –ø—Ä–æ–º–æ–∫–æ–¥–∞ –ø–æ–≤–µ—Ä—Ö –≤—Å–µ–≥–æ */
}



.promo-notification {
  position: fixed;
  top: calc(50% - 250px); /* –¶–µ–Ω—Ç—Ä–∏—Ä–æ–≤–∞–Ω–∏–µ –ø–æ –≤–µ—Ä—Ç–∏–∫–∞–ª–∏ –∏ –ø–æ–¥–Ω—è—Ç–∏–µ –Ω–∞ 200px */
  left: calc(60% - 180px); /* –¶–µ–Ω—Ç—Ä–∏—Ä–æ–≤–∞–Ω–∏–µ –ø–æ –≥–æ—Ä–∏–∑–æ–Ω—Ç–∞–ª–∏ –∏ —Å–º–µ—â–µ–Ω–∏–µ –Ω–∞ 50px –≤–ª–µ–≤–æ */
  background: rgba(20, 174, 225, 0.512);
  color: #ffffff93;
  font-size: 16px;
  padding: 14px 24px;
  border-radius: 12px;
  box-shadow: 0 4px 15px rgba(255, 255, 255, 0.29);
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 10px;
  max-width: 80%;
  text-align: center;
  text-transform: uppercase;
  letter-spacing: 1px;
  border: 2px solid #ffffff7e;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  z-index: 20;
}

/* –£–±–∏—Ä–∞–µ–º –æ–±–≤–æ–¥–∫—É –ø—Ä–∏ —Ñ–æ–∫—É—Å–µ –∏ –∞–∫—Ç–∏–≤–Ω–æ–º —Å–æ—Å—Ç–æ—è–Ω–∏–∏ */
button:focus, button:active,
.matryoshka:focus, .matryoshka:active,
.matryoshka-top img:focus, .matryoshka-top img:active,
.matryoshka-body img:focus, .matryoshka-body img:active {
  outline: none;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0); /* –£–±–∏—Ä–∞–µ–º –≤—ã–¥–µ–ª–µ–Ω–∏–µ –ø—Ä–∏ —Ç–∞–ø–µ –Ω–∞ iOS */
}

/* –£–±–∏—Ä–∞–µ–º –æ–±–≤–æ–¥–∫—É –¥–ª—è –≤—Å–µ—Ö —ç–ª–µ–º–µ–Ω—Ç–æ–≤, –µ—Å–ª–∏ –Ω—É–∂–Ω–æ */
*:focus {
  outline: none;
}

/* –°—Ç–∏–ª–∏ –¥–ª—è –ø—É–ª—å—Å–∏—Ä—É—é—â–µ–≥–æ –æ–±–ª–∞–∫–∞ */
.pulsing-cloud {
  background: rgba(173, 216, 230, 0.5);
  border-radius: 8px;
  padding: 8px 16px;
  display: inline-block;
  text-decoration: none;
  transition: all 0.3s ease;
  animation: cloud-pulse 2s infinite;
}

@keyframes cloud-pulse {
  0%, 100% {
    transform: scale(1);
    opacity: 0.8;
  }
  50% {
    transform: scale(1.05);
    opacity: 1;
  }
}

/* –û—Å—Ç–∞–ª—å–Ω—ã–µ —Å—Ç–∏–ª–∏ –æ—Å—Ç–∞—é—Ç—Å—è –±–µ–∑ –∏–∑–º–µ–Ω–µ–Ω–∏–π */