

.instruction-title {
    position: fixed; /* –§–∏–∫—Å–∏—Ä–æ–≤–∞–Ω–Ω–æ–µ –ø–æ–∑–∏—Ü–∏–æ–Ω–∏—Ä–æ–≤–∞–Ω–∏–µ */
    top: 20px; /* –û—Ç—Å—Ç—É–ø —Å–≤–µ—Ä—Ö—É */
    left: 50%; /* –¶–µ–Ω—Ç—Ä–∏—Ä–æ–≤–∞–Ω–∏–µ –ø–æ –≥–æ—Ä–∏–∑–æ–Ω—Ç–∞–ª–∏ */
    transform: translateX(-50%); /* –¢–æ—á–Ω–æ–µ —Ü–µ–Ω—Ç—Ä–∏—Ä–æ–≤–∞–Ω–∏–µ */
    font-size: 4rem;
    color: #ffffff; 
    text-shadow: 
    0px 0px 10px rgba(0, 209, 255, 0.8), 
    0px 0px 20px rgba(0, 119, 255, 0.6),
    -2px -2px 0 #00d1ff,  
    2px -2px 0 #00d1ff,
    -2px 2px 0 #00d1ff,
    2px 2px 0 #00d1ff; /* –ì—Ä–∞–¥–∏–µ–Ω—Ç–Ω–∞—è –æ–±–≤–æ–¥–∫–∞ */
    background: transparent;
    text-align: center;
    letter-spacing: 5px;
    z-index: 2000; /* –í—ã—Å–æ–∫–∏–π z-index –¥–ª—è –æ—Ç–æ–±—Ä–∞–∂–µ–Ω–∏—è –ø–æ–≤–µ—Ä—Ö –≤—Å–µ—Ö —ç–ª–µ–º–µ–Ω—Ç–æ–≤ */
  }
  
  
  /* –ê–¥–∞–ø—Ç–∞—Ü–∏—è –¥–ª—è –º–æ–±–∏–ª—å–Ω—ã—Ö —É—Å—Ç—Ä–æ–π—Å—Ç–≤ */
  @media (max-width: 768px) {
    .instruction-title {
      font-size: 2.5rem;
      top: 10px;
    }
  }
  
  @media (max-width: 480px) {
    .instruction-title {
      font-size: 1.8rem;
      top: 5px;
    }
  }
  
.instruction-overlay {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 1000;
    background-size: cover;
    background-position: center;
    background-image: url("../assets/png/index-screen4.png"); /* –§–æ–Ω —Å—Ç–∞—Ç–∏—á–Ω—ã–π */
    
}

.background-image{
    filter: blur(30px);
}
.dark-overlay {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.7);
    backdrop-filter: blur(10px);
}

.highlight-box {
    position: absolute;
    border: 3px solid #61ed25;
    border-radius: 10px;
    box-shadow: 0 0 30px #ffc800;
    transition: all 0.3s ease-in-out;
    background: transparent;
    z-index: 1002;
}

/* –ê–Ω–∏–º–∞—Ü–∏—è –ø–ª–∞–≤–∞–Ω–∏—è */
@keyframes float {
  0%, 100% {
    transform: translateY(0); /* –ò—Å—Ö–æ–¥–Ω–æ–µ –ø–æ–ª–æ–∂–µ–Ω–∏–µ */
  }
  50% {
    transform: translateY(-10px); /* –ü–æ–¥–Ω–∏–º–∞–µ–º —ç–ª–µ–º–µ–Ω—Ç –≤–≤–µ—Ä—Ö */
  }
}

/* –ü—Ä–∏–º–µ–Ω—è–µ–º –∞–Ω–∏–º–∞—Ü–∏—é –∫ –∫–∞—Ä—Ç–æ—á–∫–µ –∏–Ω—Å—Ç—Ä—É–∫—Ü–∏–∏ */
.instruction-box {
  position: absolute;
  background: rgba(255, 0, 0, 0.286);
  padding: 15px;
  border-radius: 10px;
  text-align: center;
  font-size: 18px;
  font-weight: 900;
  box-shadow: 
    0 20px 32px rgba(208, 208, 208, 0.781),
    inset 0 8px 16px rgba(0, 0, 0, 0.719);
  border: 3px solid rgba(255, 255, 255, 0.579);
  animation: float 3s ease-in-out infinite; /* –ê–Ω–∏–º–∞—Ü–∏—è –ø–ª–∞–≤–∞–Ω–∏—è */
}

/* –ü—Ä–∏–º–µ–Ω—è–µ–º –∞–Ω–∏–º–∞—Ü–∏—é –∫ –∫–Ω–æ–ø–∫–µ */
.next-button {
  background: #61ed25;
  color: black;
  border: none;
  padding: 10px 20px;
  cursor: pointer;
  font-size: 16px;
  border-radius: 5px;
  margin-top: 10px;
  font-weight: bold;
  box-shadow: 
    0 8px 5px rgba(255, 255, 255, 0.604),
    inset 0 8px 16px rgba(1, 56, 101, 0.719);
  border: 2px solid rgba(255, 255, 255, 0.947);
  transition: background-color 0.3s ease, transform 0.1s ease, box-shadow 0.3s ease;
  animation: float 3s ease-in-out infinite; /* –ê–Ω–∏–º–∞—Ü–∏—è –ø–ª–∞–≤–∞–Ω–∏—è */
}

.next-button:hover {
  background: #40b715 !important;
  color: white;
}

.next-button:active {
  transform: scale(0.95);
  background: #2e8b10 !important;
  box-shadow: 
    0 4px 16px rgba(0, 0, 0, 0.3),
    inset 0 4px 8px rgba(1, 56, 101, 0.719);
  transition: transform 0.05s ease;
}
.instruction-description {
    font-size: 16px; /* –£–º–µ–Ω—å—à–µ–Ω–Ω—ã–π —Ä–∞–∑–º–µ—Ä —Ç–µ–∫—Å—Ç–∞ */
    font-weight: 550; /* –û–±—ã—á–Ω—ã–π (–º–µ–Ω–µ–µ –∂–∏—Ä–Ω—ã–π) —à—Ä–∏—Ñ—Ç */
    color: #ccc; /* –ù–µ–º–Ω–æ–≥–æ —Å–≤–µ—Ç–ª–µ–µ –¥–ª—è –ª—É—á—à–µ–π —á–∏—Ç–∞–±–µ–ª—å–Ω–æ—Å—Ç–∏ */
    margin-top: 4px; /* –î–æ–±–∞–≤–ª—è–µ–º –Ω–µ–±–æ–ª—å—à–æ–π –æ—Ç—Å—Ç—É–ø —Å–≤–µ—Ä—Ö—É */
  }
  