/* –°—Ç–∏–ª–∏ –¥–ª—è –∑–∞–≥–æ–ª–æ–≤–∫–∞ —Ä–µ–∑—É–ª—å—Ç–∞—Ç–∞ */
.result-title {
    margin-top: 50px; /* –û—Ç—Å—Ç—É–ø —Å–≤–µ—Ä—Ö—É */
    font-size: 26px; /* –†–∞–∑–º–µ—Ä —à—Ä–∏—Ñ—Ç–∞ */
    color: #ffffff; /* –ë–µ–ª—ã–π —Ü–≤–µ—Ç —Ç–µ–∫—Å—Ç–∞ */
    text-align: center; /* –¶–µ–Ω—Ç—Ä–∏—Ä–æ–≤–∞–Ω–∏–µ —Ç–µ–∫—Å—Ç–∞ */
    background: rgba(51, 66, 161, 0.684); /* –ü–æ–ª—É–ø—Ä–æ–∑—Ä–∞—á–Ω—ã–π —Ñ–æ–Ω */
    box-shadow: 
      inset 0 6px 16px rgba(223, 223, 223, 0.477); /* –í–Ω—É—Ç—Ä–µ–Ω–Ω—è—è —Ç–µ–Ω—å –¥–ª—è –æ–±—ä—ë–º–∞ */
      border: 1px solid rgb(242, 242, 242); /* –õ—ë–≥–∫–∞—è –æ–±–≤–æ–¥–∫–∞ –¥–ª—è –æ–±—ä—ë–º–∞ */
    padding: 10px 20px; /* –û—Ç—Å—Ç—É–ø—ã –≤–Ω—É—Ç—Ä–∏ */
    border-radius: 18px; /* –ó–∞–∫—Ä—É–≥–ª–µ–Ω–Ω—ã–µ –∫—Ä–∞—è */
    display: inline-block; /* –ß—Ç–æ–±—ã –∫–æ–Ω—Ç–µ–π–Ω–µ—Ä –ø–æ–¥—Å—Ç—Ä–æ–∏–ª—Å—è –ø–æ–¥ —Ä–∞–∑–º–µ—Ä —Ç–µ–∫—Å—Ç–∞ */
  }
  
  /* –°—Ç–∏–ª–∏ –¥–ª—è –∑–∞–≥–æ–ª–æ–≤–∫–∞ –ø—Ä–∏ –ø—Ä–æ–∏–≥—Ä—ã—à–µ */
  .result-title.lose {
    margin-top: 20px; /* –£–≤–µ–ª–∏—á–µ–Ω–Ω—ã–π –æ—Ç—Å—Ç—É–ø —Å–≤–µ—Ä—Ö—É */
  }
  
  /* –ö–æ–Ω—Ç–µ–π–Ω–µ—Ä –¥–ª—è –∞–Ω–∏–º–∞—Ü–∏–∏ –ø—Ä–æ–∏–≥—Ä—ã—à–∞ */
  .lose-animation-container {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 320px; /* –í—ã—Å–æ—Ç–∞ –∫–æ–Ω—Ç–µ–π–Ω–µ—Ä–∞ */
    transform: translateY(-50px); /* –ü–æ–¥–Ω—è—Ç–∏–µ –∫–æ–Ω—Ç–µ–π–Ω–µ—Ä–∞ –Ω–∞ 50px */
  }
  
  /* –ê–Ω–∏–º–∞—Ü–∏—è –ø—Ä–æ–∏–≥—Ä—ã—à–∞ */
  .lose-animation {
    width: 180px; /* –£–≤–µ–ª–∏—á–µ–Ω–∏–µ —à–∏—Ä–∏–Ω—ã –∏–∑–æ–±—Ä–∞–∂–µ–Ω–∏—è */
    height: 180px; /* –£–≤–µ–ª–∏—á–µ–Ω–∏–µ –≤—ã—Å–æ—Ç—ã –∏–∑–æ–±—Ä–∞–∂–µ–Ω–∏—è */
    animation: pulse 1.5s infinite; /* –ü—É–ª—å—Å–∞—Ü–∏—è —Å –¥–ª–∏—Ç–µ–ª—å–Ω–æ—Å—Ç—å—é 2 —Å–µ–∫—É–Ω–¥—ã */
  }
  
  /* –ö–Ω–æ–ø–∫–∞ "–ü–æ–≤—Ç–æ—Ä–∏—Ç—å" - –±–∞–∑–æ–≤—ã–µ —Å—Ç–∏–ª–∏ */
  .restart-button-container {
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    width: 100%;
    display: flex;
    justify-content: center;
    z-index: 20;
    bottom: 20px; /* –ü–æ–¥–Ω–∏–º–∞–µ–º –∫–Ω–æ–ø–∫—É –Ω–∞ 20px */
  }
  
  /* –ö–Ω–æ–ø–∫–∞ "–ü–æ–≤—Ç–æ—Ä–∏—Ç—å" –ø—Ä–∏ –ø—Ä–æ–∏–≥—Ä—ã—à–µ (–ø–æ–¥–Ω—è—Ç–∞ –Ω–∞ 40px) */
  .restart-button-container.restart-center {
    bottom: 40px; /* –ü–æ–¥–Ω–∏–º–∞–µ–º –∫–Ω–æ–ø–∫—É –Ω–∞ 40px */
  }
  
  /* –°—Ç–∏–ª–∏ –¥–ª—è –∫–Ω–æ–ø–∫–∏ "–ü–æ–≤—Ç–æ—Ä–∏—Ç—å" */
  .restart-button {
    width: 300px;
    height: 60px;
    background-color: rgb(1, 56, 101);
    box-shadow: 
    0 6px 2px rgba(255, 255, 255, 0.604), /* –û—Å–Ω–æ–≤–Ω–∞—è —Ç–µ–Ω—å */
    inset 0 8px 16px rgba(1, 56, 101, 0.719); /* –í–Ω—É—Ç—Ä–µ–Ω–Ω—è—è —Ç–µ–Ω—å –¥–ª—è –æ–±—ä—ë–º–∞ */
  border: 1px solid rgba(255, 255, 255, 0.947); /* –õ—ë–≥–∫–∞—è –æ–±–≤–æ–¥–∫–∞ –¥–ª—è –æ–±—ä—ë–º–∞ */
  
    color: white;
    font-weight: 100; /* –ú–µ–Ω—å—à–∞—è –∂–∏—Ä–Ω–æ—Å—Ç—å —Ç–µ–∫—Å—Ç–∞ */
    font-size: 18px;
    border-radius: 10px;
    margin: 0px 0;
    transition: background-color 0.3s ease, transform 0.3s ease;
  
  }
  
  @keyframes pulse {
    0%, 100% {
      transform: scale(1);
    }
    50% {
      transform: scale(1.05); /* –£–≤–µ–ª–∏—á–µ–Ω–∏–µ –ø—Ä–∏ –ø—É–ª—å—Å–∞—Ü–∏–∏ */
    }
  }
  
  .restart-button:hover {
    background-color: #2d487758; /* –ë–æ–ª–µ–µ —Å–≤–µ—Ç–ª—ã–π —Å–∏–Ω–∏–π –ø—Ä–∏ –Ω–∞–≤–µ–¥–µ–Ω–∏–∏ */
    
    box-shadow: 0 6px 16px rgba(0, 0, 0, 0.3), /* –£—Å–∏–ª–µ–Ω–∏–µ —Ç–µ–Ω–∏ */
    inset 0 8px 16px rgba(1, 56, 101, 0.719); /* –í–Ω—É—Ç—Ä–µ–Ω–Ω—è—è —Ç–µ–Ω—å –¥–ª—è –æ–±—ä—ë–º–∞ */
  
  }
  
  .restart-button:active {
    animation: none; /* –û—Ç–∫–ª—é—á–∞–µ–º –∞–Ω–∏–º–∞—Ü–∏—é –ø—Ä–∏ –Ω–∞–∂–∞—Ç–∏–∏ */
    transform: scale(0.95); /* –°–∏–ª—å–Ω–æ–µ —É–º–µ–Ω—å—à–µ–Ω–∏–µ –ø—Ä–∏ –Ω–∞–∂–∞—Ç–∏–∏ */
    background-color: #142b61; /* –¢–µ–º–Ω–æ-—Å–∏–Ω–∏–π —Ü–≤–µ—Ç –ø—Ä–∏ –Ω–∞–∂–∞—Ç–∏–∏ */
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.2); /* –£–º–µ–Ω—å—à–µ–Ω–∏–µ —Ç–µ–Ω–∏ –ø—Ä–∏ –Ω–∞–∂–∞—Ç–∏–∏ */
    transition: transform 0.05s ease; /* –£—Å–∫–æ—Ä–µ–Ω–Ω—ã–π –ø–µ—Ä–µ—Ö–æ–¥ –¥–ª—è —ç—Ñ—Ñ–µ–∫—Ç–∞ –Ω–∞–∂–∞—Ç–∏—è */
  }